.small-header {
    background-color: #f4f4f4; /* Or any other color */
    padding: 20px;
    text-align: center;
  }
  
  .project-content {
    display: flex;
    justify-content: space-between;
    max-width: 1200px;
    margin: 20px auto;
    padding: 20px;
  }
  
  .project-description {
    flex: 1;
    padding-right: 20px; /* Adjust spacing between text and image */
  }
  
  .project-image {
    flex: 1;
  }
  
  .project-image img {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
  