.project-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; /* Center the tiles */
    padding: 20px;
    max-width: 1200px; /* Maximum width of the container */
    margin: auto; /* Center the container */
  }
  
  .project-tile {
    flex: 1 1 300px; /* Flexible basis with a minimum width */
    max-width: calc(33.333% - 20px); /* Maximum of 3 tiles in a row */
    height: 350px; /* Fixed height for each tile */
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    margin: 10px;
    position: relative;
  }

  .project-image  {
    width: 100%;
    height: 80%; /* Adjust the height percentage as needed */
    overflow: hidden;
  }
  
  .project-tile img {
    width: 100%; /* Ensures the image stretches to cover the container */
    height: 100%; /* Ensures the image stretches to cover the container */
    object-fit: cover; /* Image will cover the container, might be cropped */
    display: block;
    transition: transform 0.3s ease;
  }
  
  .project-tile h3 {
    text-align: center;
    margin-top: 10px;
  }
  
  .project-tile a {
    color: inherit;
    text-decoration: none;
  }
  
  .project-caption {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 80%; /* Match the image height */
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    opacity: 0;
    transition: opacity 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  
  .project-tile:hover img {
    transform: scale(1.05);
  }
  
  .project-tile:hover .project-caption {
    opacity: 1;
  }
  
  @media (max-width: 900px) {
    .project-tile {
      max-width: calc(50% - 20px); /* 2 tiles in a row */
    }
  }
  
  @media (max-width: 600px) {
    .project-tile {
      max-width: 100%; /* 1 tile in a row */
    }
  }
  